:root {
  --header-bg-color: rgb(26, 26, 26);
  --main-font-family: Lucida Grande, Lucida Sans Unicode, Lucida Sans, Geneva,
    Verdana, sans-serif;
}

.thumbnail {
  width: 300px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background: var(--header-bg-color);
}

.thumbnail__border-radius_round {
  border-radius: 20px 20px 0 0;
}

/* Image part styles */

.image {
  height: 187.5px;
  position: relative;
}

.image img {
  width: 100%;
  transition: 0.2s;
  height: 187.5px;
  object-fit: cover;
}

.image__hover_on {
  opacity: 0.1;
}

.image__text__on {
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  text-align: center;
}

.image__text__off {
  display: none;
}
.cursor_pointer {
  cursor: pointer;
}

/* Text part styles */

.text {
  text-align: center;
  flex: 1 0 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
  margin: 10px;
  background: var(--header-bg-color);
}
