/* VARIABLES AND SHARED STYLES */

:root {
  --header-bg-color: rgb(26, 26, 26);
  --main-font-color: #d1fafe;
}

a {
  color: #d1fafe;
  text-decoration: none;
}

/* HEADER */

.header {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--header-bg-color);
}

.header__logo-text {
  color: white;
  font-size: 28px;
  letter-spacing: 10px;
}

/* BODY */

.body {
  max-width: 960px;
  margin: auto;
  padding: 25px 10px;
  height: 100%;
  color: #d1fafe;
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  grid-column-gap: 25px;
  grid-row-gap: 25px;
  justify-content: center;
  font-size: 0.9em;
}
