:root {
  --colo-backed: rgb(101, 101, 101)
}

.hero {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  margin: auto; 
  color:#d1fafe;  
}

.image-container {  
  flex: 1 0 auto;
  max-width: 960px; 
  margin: auto;
}

.image-container img {
  width: 100%;
  object-fit: cover;
  margin-top: 17px;
  margin-bottom: 10px;
}

.text {
  background: rgb(26,26,26);
  padding: 10px 20px 20px 20px;
  color: white;
}

.container__width_960 {
  max-width: 960px;
  margin: auto
}

.q {
  justify-content: center;
    display: flex;
    font-size: 1.5em;
    text-align: center;
    padding: 20px;
    border: 1px solid white;
    margin: 10px
}

.hero__bio {
  padding: 20px
}
